import React, { useState, useEffect } from 'react';
import axios from 'axios';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import {useLocation, useNavigate} from 'react-router-dom';

const Emailauthnum=()=>{

  const navigate = useNavigate();
  const location = useLocation();
  const [authCode, setauthCode] = useState('');
  const [email, setEmail] = useState('');
  
  useEffect(()=>setEmail(location.state.email));
  

  // const handleEmailChange = (e) => {
  //   setEmail(e.target.value);
  // };

  const handleauthCodeChange = (e) => {
    setauthCode(e.target.value);
  };

  const handleauthCodeSubmit = (e) => {
    e.preventDefault();
    // const { email } = location.state;
    const data = {
      authCode,
      email
    }


    //     .
    axios
      .post('http://203.247.40.158:8080/api/email/send/auth/check', data, {"Content-Type": 'application/json'})
      .then((response) => {
        //   ,   
        if (response.status === 200) {
          navigate('/signup', {state:{authCode: authCode, email: email}});
        }
        
      })
      .catch((error) => {
        alert('   :', error);
      });
  };

	return(
		<Container component="main" maxWidth="sm">
      <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
      >
        <Typography component="h1" variant="h6" marginBottom="96px" fontSize="24px">
                .
        </Typography>
      </Box>
      <Box sx={{
              maxWidth:"385px",
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              margin: "0 auto"
            }} >
		<TextField
        id="standard-email-input"
        sx={{mb:"24px"}}
        // label=" " 
        value={email}
        required 
        fullWidth
        name="email"
        autoComplete="email"
        variant="standard"
        // autoFocus
        disabled
        />
		<TextField 
        id="standard-email-input"
        sx={{mb:"24px"}}
        label=" " 
        // value={authCode}
        onChange={handleauthCodeChange}
        required 
        fullWidth
        name="number"
        autoComplete="number"
        variant="standard"
        autoFocus
        />
      
      <Button type="submit" onClick={handleauthCodeSubmit} fullWidth variant="contained"
      sx={{mt:3, mb:2, backgroundColor:"black", height:"48px"}}>
        
      </Button>
      </Box>
		</Container>
	)
}

export default Emailauthnum